.heading-hero {
    font-size: 64px;
    font-weight: 700;
    color: #7C76BB;
    line-height: 1.2;
    /* letter-spacing: 0%; */
}

.para-hero {
    font-size: 18px;
    font-weight: 400;
    /* Regular */
    ;
    line-height: 1.3;
    /* letter-spacing: 0%; */
}


.btn-hero1 {
    text-decoration: none;
    padding: 5px 15px;
    border-radius: 8px;
    background-color: #7C76BB;
    border: 2px solid transparent;
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    line-height: normal;

}

.btn-hero1:hover {
    background-color: #fff;
    border: 2px solid #7C76BB;
    color: #7C76BB;
    transition: 300ms;
}

.btn-hero2 {
    text-decoration: none;
    padding: 5px 15px;
    border-radius: 8px;
    border: 2px solid #7C76BB;
    color: #7C76BB;
    font-size: 18px;
    font-weight: 500;
    line-height: normal;
}

.btn-hero2:hover {
    background-color: #7C76BB;
    border: 2px solid transparent;
    color: #fff;
    transition: 300ms;
}

.arrow-hero {
    width: 100%;
    max-width: 20px;
}

.img-business {
    width: 100%;
}


/* mobile Device std size
@media only screen and (max-width: 600px) {
   
} */
@media only screen and (max-width: 1200px) {
    .hero-section {
        padding: 40px 0;
    }
}


@media only screen and (max-width: 992px) {
    .hero-section {
        padding: 40px 0;
    }

    .heading-hero {
        font-size: 36px;
    }

    .para-hero {
        font-size: 16px;
    }

    .btn-hero1 {
        font-size: 16px;
    }

    .btn-hero2 {
        font-size: 16px;
    }
}

@media only screen and (max-width: 600px) {
    .hero-section {
        padding: 30px 0;
    }

    .heading-hero {
        font-size: 36px;
    }

    .para-hero {
        font-size: 16px;
    }

    .btn-hero1 {
        font-size: 16px;
    }

    .btn-hero2 {
        font-size: 16px;
    }

    .arrow-hero {
        max-width: 15px;
    }
}