.heading-testimonial {
    font-size: 48px;
    font-weight: 700;   /* bold 700px */
    color: #7C76BB;
    line-height: 1;
    letter-spacing: 0;
}

.img-testimonial {
    width: 100%;
}

.quote {
    width: 40px;
    margin-bottom: 20px;
}

.review {
    font-size: 14px;
    font-weight: 400;
    color: #717171;
    line-height: 1.3;
}

.review-bold {
    font-size: 14px;
    font-weight: 600;
    color: #717171;
    line-height: 1.3;
}

.review-customer {
    font-size: 14px;
    font-weight: 600;
    color: #7C76BB;
    line-height: 1.3;
}
.memoji-top-bg1 {
    background-color: #B4D1C4;
    border-radius: 50%;
    width: 100%;
    max-width: 60px;
    height: 60px;
}
.memoji-bg1 {
    background-color: #B4D1C4;
    border-radius: 50%;
    width: 100%;
    max-width: 80px;
    height: 80px;
}

@media only screen and (max-width: 992px) {
    .heading-testimonial {
        font-size: 36px;
    }
    .review {
        font-size: 12px;
    }
    
    .review-bold {
        font-size: 12px;
    }
    
    .review-customer {
        font-size: 12px;
    }
    .memoji-bg1 {
        width: 100%;
        max-width: 60px;
        height: 60px;
    }
}

