.heading-services {
    font-size: 48px;
    font-weight: 700;
    color: #7C76BB;
    line-height: 1;
    /* letter-spacing: 0%; */
}

.para-services {
    font-size: 18px;
    font-weight: 400;    /* Regular */;
    line-height: 1.3;
    /* letter-spacing: 0%; */
  }

.bg-card {
    background-color: #dcf0ff;
}
.card-heading {
    color:#7C76BB;
    font-size: 22px;
    font-weight: 600;    /* Semibold */;
    line-height:normal;
}

.card-para {
    font-size: 16px;
    font-weight: 400;    /* Regular */;
    line-height: 1.3;
}

.img-card {
    width:100%;
}

.btn-card {
    text-decoration: none;
    padding: 5px 15px;
    border-radius: 8px;
    border: 2px solid #7C76BB;
    color: #7C76BB;
    background-color: #fff;
    font-size: 18px;
    font-weight: 500;
    line-height: 2;
}

.btn-card:hover {
    background-color: #7C76BB;
    border: 2px solid transparent;
    color: #fff;
    transition: 3s;
}

/* .card-padding {
    padding: 20px 10 50px 10px !important;
} */
.services-section {
    padding :20px 0 40px 0;
}

@media only screen and (max-width: 992px) {
    .heading-services {
        font-size: 36px;
    }
    .para-services {
        font-size: 16px;
    }
    .card-heading {
        font-size: 22px;
        line-height: 1.1;
    }
    .card-para {
        font-size: 12px;
        line-height: 1.2;
    }
    .btn-card {
        font-size: 16px;
        line-height: normal;
    }
    /* .card-padding {
        padding: 20px 10 45px 10 !important;
    } */
    .img-card {
        padding-top: 20px;
    }
}