.bg-footer {
    background-color: #dcf0ff;
    padding: 30px 0 0 0;
}

.logo-footer {
    width: 100%;
    max-width: 250px;
}

.para-footer {
    font-size: 14px;
    font-weight: 400;    /* Regular */;
    line-height: 1.3;
    margin-bottom: 20px;
    /* letter-spacing: 0%; */
}

.footer-title {
    font-size: 16px;
    font-weight: 600;
    color: #7C76BB;
}

.footer-li {
    list-style-type: none;
    font-size: 14px;
    padding-left: 0;
}

.footer-link a{
    display: block;
    text-decoration: none;
    color: #606163;
}

.footer-icon {
    font-size: 20px;
    color: #7C76BB;
}

.footer-terms{
    font-size: 12px;
    color: #7C76BB;
    font-weight: 500;
}

