.details-section {
    padding: 40px 0;
}

.img-detail {
    width: 100%;
}

.heading-details {
    font-size :48px;
    font-weight: 600;      /* bold 700px */
    line-height: 1;
    letter-spacing: 0%;
    color: #7C76BB;
}

.btn-detail {
    text-decoration: none;
    padding: 5px 15px;
    border: 2px solid #7C76BB;
    color: #7C76BB;
    border-radius: 8px;
    font-size: 18px;
    font-weight: 500;
    line-height: normal;
}

.btn-detail:hover {
    background-color: #7C76BB;
    border: 3px solid transparent;
    color: #fff;
    transition: 300ms;
}

.arrow-details {
    width: 100%;
    max-width: 20px;
}

@media only screen and (max-width: 992px) {
    .heading-details {
        font-size: 36px;
    }
    .btn-detail {
        font-size: 16px;
    }
}

@media only screen and (max-width: 992px) {
    .arrow-details {
        max-width: 15px;
    }
}


