.logo-header {
    width: 100%;
    max-width: 160px;
}

.navbar-brand {
    font-size: 20px;
    color: #7C76BB;
    font-weight: 600;  /* Semibold*/
}

.navbar-brand:hover {
    color: #FF87B0;
}


.global-nav .nav-link {
    color: #7C76BB;
    font-size: 18px;
    font-weight: 500;  /* medium*/
    line-height:normal;  
}

.global-nav .nav-link:hover {
    color: #FF87B0;
}

.btn-header {
    padding: 5px 15px;
    border-radius: 8px;
    background-color: #7C76BB;
    border: 2px solid transparent;
    color: #fff;
    font-size: 18px;
    font-weight:500;    /* medium*/
    line-height: normal;
    text-decoration: none;
}

.btn-header:hover {
    background-color: #fff;
    border: 2px solid #7C76BB;
    color: #7C76BB;
    transition: 300ms;
}

/* Medium Device  size
@media (min-width: 768px) and (max-width: 991px) {
   
} */

@media only screen and (max-width: 992px) {
    .global-nav .nav-link {
        font-size: 14px;
         
    }
    .logo-header {
        max-width: 150px;
    }
    .btn-header {
        font-size: 14px;
    }
}
