*, html, body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.color-pink {
    color: #FF87B0;
}

.color-purple {
    color: #7C76BB;
}

/* start common css in five section */
.sub-heading {
  font-size: 18px;
  color: #FF87B0;
}

.heading-businessShop {
  font-size: 48px;
  font-weight: 600;
  color: #7C76BB;
  line-height: 1.2;
  /* letter-spacing: 0%; */
}

.para-businessShop {
  font-size: 18px;
  font-weight: 400;    /* Regular */;
  line-height: 1.3;
  /* letter-spacing: 0%; */
}

.portfolio-section {
  padding-bottom: 40px;
}

.see-live-border {
  text-decoration: none;
  border-bottom: 3px solid #7C76BB;
 
}

.case-study-border {
  text-decoration: none;
  border-bottom: 3px solid #d6d5ea;
}

.btn-text {
  font-size: 16px;
  color: #000000;
  opacity: 0.61;
}
/* end common css in four section */

@media only screen and (max-width: 992px) {
  .heading-businessShop {
    font-size: 28px;
    line-height: 1.3;
  }
  .para-businessShop {
    font-size: 16px;
  }
  .btn-text{
    font-size: 14px;
  }
}