.brand-para {
    font-size:20px;
    font-weight: 600; /* semibold */
}

.brand-icon {
    font-size: 18px;
    font-weight: 500;
}

.brand-size {
    width: 100px;
}

.brand-section {
    padding-bottom: 40px;
}

@media only screen and (max-width: 992px) {
    .brand-section {
        padding-bottom: 30px;
    }
}


@media only screen and (max-width: 600px) {
    .brand-icon {
        display: none;
    }
    .brand-size {
        margin-bottom: 10px;
    }
    /* .brand-section {
        padding-bottom: 30px;   No need to write for mobile view it will take this value from medium device 
    } */
}